  .slider-wrapper {
    width: 95%;
    height: 470px;
    margin: 0px auto 0;    
    overflow: hidden;
    border-radius: 4px !important;
    text-align: center;
  }
  
  .slider img{
      width: auto;
      height: 500px;
      margin: 3% 0;
  }
  
  .slider {
    position: relative;
  }

  .slider-item, .hide {
    float: left;
    /* position: relative; */
    text-transform: uppercase;
    color: #fff;
    line-height: 250px;
    font-size: 20px;
    background: #383838;
    text-align: center;
    border-radius: 4px !important;
    -webkit-transition: all .2s linear;
    transition: all .2s linear;
  }
  
  .buttons-wrapper {
    width: 95%;
    margin: -15px 2% 0;
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 55%;
    /* margin-top: -15px; */
  }
  
  .buttons-wrapper .prev-button, .buttons-wrapper .next-button {
    background-image: url(http://static.birgun.net/static/images/slider-arrow-left.svg);
    background-size: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    height: 30px;
    width: 30px;
    border-radius: 4px;
    color: #fff;
    border: none;
    cursor: pointer;
    outline: none;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
  }
  
  .buttons-wrapper .next-button {
    background-image: url(http://static.birgun.net/static/images/slider-arrow-right.svg);
  }
  
  .buttons-wrapper button:hover {
    background-color: rgba(0, 0, 0, 0.4);
  }
  
  .pulse-button {
    position: relative;
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -50px;
    display: block;
    width: 40px;
    height: 40px;
    font-size: 1.3em;
    font-weight: light;
    font-family: 'Trebuchet MS', sans-serif;
    text-transform: uppercase;
    text-align: center;
    line-height: 100px;
    letter-spacing: -1px;
    color: white;
    border: none;
    border-radius: 50%;
    background: #ffffff;
    border: 1px solid #000;
    cursor: pointer;
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.5);
    -webkit-animation: pulse 1.5s infinite;
            animation: pulse 1.5s infinite;
  }
  
  .pulse-button:hover {
    -webkit-animation: none;
            animation: none;
  }
  
  @-webkit-keyframes pulse {
    0% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
    }
    70% {
      -webkit-transform: scale(1);
      transform: scale(1);
      box-shadow: 0 0 0 50px rgba(90, 153, 212, 0);
    }
    100% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
      box-shadow: 0 0 0 0 rgba(90, 153, 212, 0);
    }
  }
  
  @keyframes pulse {
    0% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
    }
    70% {
      -webkit-transform: scale(1);
      transform: scale(1);
      box-shadow: 0 0 0 50px rgba(90, 153, 212, 0);
    }
    100% {
      -webkit-transform: scale(0.9);
      transform: scale(0.9);
      box-shadow: 0 0 0 0 rgba(90, 153, 212, 0);
    }
  }

  .modal-dialog{
