  }

@media (min-width: 992px){
  .slider-wrapper {
    width: 35%;
    height: 550px;
    margin: 0 auto 0;    
    position: relative;
  }
  .buttons-wrapper {
    width: 75%;
    margin: -15px 15% 0;
  }
  .modal-dialog{
    margin: 5rem auto;
  }
  .modal-body img{
    width: 490px;
    height: 350px;;
  }
  .pulse-button{
    top: 1%;
    /* margin-top: -70px; */
  }
}
